    name: string|null = null;
    supportMe = true;
    email: string|null = null;
    disableSending = false;

    @Prop() photoId!: string;

    async mounted(){
        if(this.$route.query.redirect_status) {
            const status = this.$route.query.redirect_status.toString();
            if(status === 'succeeded')
                alert(this.$t('galleryMessage.successDonation'));
        }
    }

